import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Table,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
import moment from 'moment'
import {BBC} from 'Mock'
const ORDER_LIST = 'wms/packageList'
const ORDER_LISTNUM = 'wms/packageListNum'

@connect(
  state => ({...state.packageList}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '序号',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '商品名称',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '商品条码',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '单价（元）',
      dataIndex: 'price',
      key: 'price',
      align: 'center'
    }, {
      title: '单位',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '数量',
      dataIndex: 'qty',
      key: 'qty',
      align: 'center'
    }, {
      title: '总金额（元）',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }]
    this.columns2 = [{
      title: '序号',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '商品名称',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '商品条码',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '单位',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '数量',
      dataIndex: 'qty',
      key: 'qty',
      align: 'center'
    }, {
      title: '总金额（元）',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }]
  }
  componentDidMount() {
    const {match: {params: {id}}} = this.props
    this._getAsyncData(id)
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData(type) {
    try {
      let ids = []
      if (type == 1) {
        ids = getVariant(ORDER_LIST) && getVariant(ORDER_LIST).toString()
      } else {
        ids = getVariant(ORDER_LISTNUM) && getVariant(ORDER_LISTNUM).toString()
      }
      if (!ids.length) return (0, this._closeWindow)(message.error('订单编号错误'))
      const {handleGetDetailPrint} = this.props
      handleGetDetailPrint({searchParams: {ids}}, type)
    } catch (err) {
      console.error(err)
    }
  }
  /* 关闭窗口 */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /* 打印 */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
    try {
      let ids = []
      const {match: {params: {id}}} = this.props
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error('更新打印次数失败，订单编号错误'))
      const {handleUpdatePrintNum} = this.props
      handleUpdatePrintNum({data: ids}, id)
    } catch (err) {
      console.error(err)
    }
  }

  // 画条形码
  componentDidUpdate() {
    const {printData, match: {params: {id}}} = this.props
    id == 1 ? printData.map((o) => {
      JsBarcode(`#key${o.orderCode}`, o.orderCode, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
      JsBarcode(`#key${o.outCode}`, o.outCode, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    }) : printData.map((o) => {
      JsBarcode(`#key${o.header.outCode}`, o.header.outCode, {
        width: 1.5,
        height: 30,
        fontSize: 12
        // displayValue: false
      })
      JsBarcode(`#key${o.header.orderCode}`, o.header.orderCode, {
        width: 1.5,
        height: 30,
        fontSize: 12
        // displayValue: false
      })
    })
  }
  render() {
    const {printData, tableLoading, match: {params: {id}}, printLoading} = this.props

    return (
      <React.Fragment>
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button> : (printLoading ? <h2 style={{textAlign: 'center'}}>正在加载打印数据</h2> : <h2 style={{textAlign: 'center'}}>暂无打印数据</h2>)
        }
        {

          id == 1 ? printData.map((o) => <div className="PageBreakAfter" style={{textAlign: 'center', display: 'flex', justifyContent: 'center', alignContent: 'center'}} key={o.orderCode}>
            <div>
              <img id={`key${o.orderCode}`} />
              <div style={{borderBottom: '1px solid #000', padding: '16px', marginBottom: '16px'}}>
                <h2 style={{fontSize: '22px', fontWeight: 'bold'}}>包裹：{o.orderCode}</h2>
              </div>
              <h2 style={{fontSize: '22px', fontWeight: 'bold', lineHeight: '36px', marginBottom: '16px'}}>{o.stationName}</h2>
              <div style={{borderTop: '1px solid #000', borderBottom: '1px solid #000', marginBottom: '32px', display: 'flex', alignItems: 'center'}}>
                <h2 style={{fontSize: '22px', fontWeight: 'bold', width: '48px'}}>收</h2>
                <div style={{borderLeft: '1px solid #000', padding: '16px', lineHeight: '24px', textAlign: 'left'}}>
                  <div>
                    <h2 style={{fontSize: '22px', fontWeight: 'bold'}}>{o.receiveName}：{o.receivePhoneNumber}</h2>
                  </div>
                  <div>
                    <h2 style={{fontSize: '22px', fontWeight: 'bold'}}>地址：{o.receiveAddress}</h2>
                  </div>
                </div>
              </div>

              <img id={`key${o.outCode}`} />
              <div style={{padding: '16px', marginBottom: '16px'}}>
                <h2 style={{fontSize: '22px', fontWeight: 'bold', marginBottom: '0'}}>{o.outCode}</h2>
              </div>
              <div style={{borderTop: '1px solid #000', borderBottom: '1px solid #000', marginBottom: '32px', display: 'flex', alignItems: 'center'}}>
                <h2 style={{fontSize: '22px', fontWeight: 'bold', width: '48px'}}>寄</h2>
                <div style={{borderLeft: '1px solid #000', padding: '16px', lineHeight: '24px', textAlign: 'left'}}>
                  <div>
                    <h2 style={{fontSize: '22px', fontWeight: 'bold'}}>{o.shopName}：{o.shopPhoneNumber}</h2>
                  </div>
                  <div>
                    <h2 style={{fontSize: '22px', fontWeight: 'bold'}}>地址：{o.shopAddress}</h2>
                  </div>
                </div>
              </div>
            </div>
          </div>) : printData.map((o, i) => <section className="outboundOrderTemplate PageBreakAfter" key={o.header.outCode}>
            <div style={{position: 'relative', height: '80px', marginBottom: '20px', borderBottom: '1px solid #000', marginTop: '30px'}}>
              <h2 className="title">出库单打印11111-{o.header.businessTypeDesc}</h2>
              <img id={`key${o.header.orderCode}`} style={{position: 'absolute', top: '-5px', left: '30px'}} />
              <img id={`key${o.header.outCode}`} style={{position: 'absolute', top: '-5px', right: '30px'}} />
              <div style={{position: 'absolute', top: '10px', right: '10px', fontSize: '12px'}}>
                {/* <p style={{margin: '0px'}}>打印次数：{o.header.printTimes}</p> */}
              </div>
            </div>
            <div className='header'>
              {
                BBC.indexOf(o.header.businessType) != -1 ? <Row type="flex" justify="start">
                  <Col span={8} className='delBox'>
                    <label>包裹单：</label>
                    <span>{o.header.orderCode || ''}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>出库单：</label>
                    <span>{o.header.outCode || ''}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>站点：</label>
                    <span>{`${o.header.stationCode || ''}-${o.header.stationName || ''}`}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>商品种类：</label>
                    <span>{o.header.productTypeNum || ''}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>收货人：</label>
                    <span>{o.header.receiveName || ''}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>联系电话：</label>
                    <span>{o.header.receivePhoneNumber || ''}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>收货地址：</label>
                    <span>{o.header.receiveAddress || ''}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>打印日期：</label>
                    <span>{moment().format('YYYY-MM-DD HH:mm:ss')}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>计划发货日期：</label>
                    <span>{o.header.planDate || ''}</span>
                  </Col>
                </Row> : <Row type="flex" justify="start">
                    <Col span={8} className='delBox'>
                      <label>出库单：</label>
                      <span>{o.header.outCode || ''}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>包裹单：</label>
                      <span>{o.header.orderCode || ''}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>配送门店：</label>
                      <span>{`${o.header.shopCode || ''}-${o.header.shopName || ''}`}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>门店地址：</label>
                      <span>{o.header.shopAddress || ''}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>外部单号：</label>
                      <span>{o.header.externalCode || ''}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>打单人：</label>
                      <span>{o.header.createBy || ''}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>商品种类：</label>
                      <span>{o.header.productTypeNum || ''}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label>打印日期：</label>
                      <span>{moment().format('YYYY-MM-DD HH:mm:ss')}</span>
                    </Col>
                  </Row>
              }
            </div>
            <div className='foot'>
              <Table
                rowKey={record => record.id}
                loading={tableLoading}
                columns={BBC.indexOf(o.header.businessType) != -1 ? this.columns2 : this.columns}
                dataSource={o.items}
                pagination={false}
                style={{marginTop: '6px'}}
                bordered
                size="middle"
              />
            </div>
          </section>)
        }
        <div className="printLoadingSty">
          <Spin spinning={printLoading} size="large" />
        </div>
      </React.Fragment>
    )
  }
}



